___CSS_LOADER_EXPORT___.push([module.id, `* {
    padding: 0;
    margin: 0;
    border: 0;
    box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-size: inherit;
    font-weight: inherit;
}

a {
    color: inherit;
    text-decoration: none;
    font-size: inherit;
    font-weight: inherit;
}

*::before,
*::after {
    box-sizing: border-box;
}

body {
    min-height: 100vh;
    scroll-behavior: smooth;
    text-rendering: optimizeSpeed;
    line-height: 1.5;
    font-family: 'Poppins', sans-serif;
}

* ul,
* ol {
    list-style: none;
}

img {
    max-width: 100%;
    display: block;
}

input,
button,
textarea,
select {
    font: inherit;
    color: inherit;
}

@font-face {
    font-family: StratosSkyeng;
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

body {
    font-family: 'StratosSkyeng';
    background-color: #004980;
    padding: 22px;
}

h1 {
    color: #004980;
    font-size: 40px;
    line-height: 48px;
}

.button {
    color: #fff;
    display: flex;
    width: 246px;
    height: 48px;
    justify-content: center;
    align-items: center;
    border-radius: 12px;
    background: #7ac100;
    transition: all 0.2s;
    cursor: pointer;
}
.button:hover {
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
}

.button:active {
    opacity: 0.7;
}

.start-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 90vh;
}
.start-box {
    width: 480px;
    height: 459px;
    border-radius: 12px;
    background: #c2f5ff;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.start-box-text {
    text-align: center;
    width: 208px;
    margin: 52px 48px;
}
.start-box-flex {
    display: flex;
    gap: 24px;
    position: relative;
}

.start-box-number {
    display: flex;
    background-color: #fff;
    width: 98px;
    height: 98px;
    border-radius: 12px;
    font-size: 64px;
    line-height: 72px;
    color: #0080c1;
    transition: transform 0.3s;
    justify-content: center;
    align-items: center;
}

.start-box-number:hover {
    transform: scale(1.15);
}
.start-box-radio:checked + .start-box-number {
    background-color: #004980;
    color: #fff;
}

.start-box-radio {
    width: 0;
    height: 0;
    position: absolute;
    opacity: 0;
    z-index: -1;
}

.start-box-button {
    margin: auto;
}
.game {
    max-width: 1024px;
    margin: auto;
    display: none;
}
.game-flex {
    display: flex;
    justify-content: space-between;
    height: 148px;
    width: 100%;
    margin-top: 20px;
}

.game-time {
    color: #fff;
    font-size: 64px;
    line-height: 72px;
    position: relative;
}

.game-time-min,
.game-time-sek {
    font-size: 16px;
    line-height: 32px;
    position: absolute;
    top: -23px;
}

.game-time-sek {
    right: 2%;
}
.game-time-min {
    left: 25%;
}

.game-container {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 15px;
}

.card-container {
    position: relative;
    width: 95px;
    height: 133px;
}
.card-back {
    width: 95px;
    height: 133px;
    border-radius: 4px;
    position: absolute;
    transition: transform 0.7s ease-out;
    backface-visibility: hidden;
}
.card-back img {
    border-radius: 4px;
}

.card-front {
    width: 95px;
    height: 133px;
    border-radius: 4px;
    transition: transform 0.7s ease-out;
    backface-visibility: hidden;
    border-radius: 4px;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    position: relative;
    justify-content: center;
    align-items: center;
    transform: rotateY(180deg);
}
.card-container.flipped .card-back {
    transform: rotateY(180deg);
}
.card-container.flipped .card-front {
    transform: rotateY(0deg);
}

.card-value-top {
    position: absolute;
    display: flex;
    flex-direction: column;
    top: 0;
    left: 5%;
    align-items: center;
    justify-content: center;
}
.card-value-top svg {
    margin-top: -2px;
}

.card-value-bottom {
    display: flex;
    flex-direction: column;
    transform: rotate(180deg);
    position: absolute;
    bottom: 0;
    right: 5%;
    align-items: center;
    justify-content: center;
}
.card-value-bottom svg {
    margin-top: -2px;
}

.result {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 2;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.3);
    visibility: hidden;
    opacity: 0;
    transition: all 0.4s ease;
    display: flex;
    align-items: center;
    justify-content: center;
}
.result_modal {
    width: 480px;
    height: 460px;
    background-color: #c2f5ff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 12px;
    margin: 0 auto;
}
.result_modal h1 {
    margin: 9px 0 28px 0;
}
.result_modal button {
    margin-bottom: 48px;
}

.result-text {
    font-size: 24px;
    line-height: 32px;
}
.result-time {
    font-size: 64px;
    line-height: 72px;
    margin-top: 10px;
    margin-bottom: 40px;
}
